import React from 'react';

export default class AboutMe extends React.Component{
    render(){
        return(
            <section class="elementor-section elementor-element">
                <div class="elementor-container elementor-column-gap-no">
                    <div class="elementor-column elementor-col-100 elementor-element">
                        <div class="elementor-widget-wrap elementor-element-populated">
                            <div class="elementor-element elementor-widget">
                                <div class="elementor-widget-container">
                                    <div class="pb-0 pb-sm-2 section">
                                        <h2 class="title title--h2 first-title title__separate"> <span> About Me </span></h2>
                                        <div class="single-post-text">
                                            <div>
                                                <p>I'm a Developer from Paris, France, working in fullstack web development and innovation (Data Analysis, Blochain, etc.).</p>
                                                <p>My job is to build your website so that it is functional and user-friendly. I also build dashboards easy-to-use gathering all informations you want and need.</p>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        )
    }
}